:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
  --loader-color: #4848d6;
}

body, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

body {
  letter-spacing: .03em;
  background-color: #eceff1;
  font-family: Roboto, sans-serif;
}

.list {
  margin-top: 30px;
}

.list-item:not(:last-child) {
  margin-bottom: 7px;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 30px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 10px;
  }
}

.search-form {
  background-color: #4848d6;
  justify-content: center;
  margin-bottom: 15px;
  padding-top: 7px;
  padding-bottom: 7px;
  font-size: 15px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .search-form {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

.search-form > input {
  width: 200px;
  border-radius: 5px;
  margin-right: 2px;
  padding: 7px;
}

@media screen and (min-width: 768px) {
  .search-form > input {
    width: 350px;
    margin-right: 5px;
    padding: 10px;
  }
}

.search-form > button {
  width: 40px;
  cursor: pointer;
  border-radius: 5px;
}

@media screen and (min-width: 768px) {
  .search-form > button {
    width: 60px;
  }
}

.search-form > button:hover .search-form-icon, .search-form > button:focus .search-form-icon {
  fill: #3f3fbd;
}

.search-form-icon {
  width: 15px;
  height: 15px;
  transition: fill var(--animation-duration) var(--timing-function);
}

@media screen and (min-width: 768px) {
  .search-form-icon {
    width: 20px;
    height: 20px;
  }
}

.gallery {
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 20px;
  display: flex;
}

.photo-card {
  height: auto;
  transition: transform var(--animation-duration) var(--timing-function);
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  flex-basis: 100%;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .photo-card {
    flex-basis: calc(50% - 10px);
  }
}

@media screen and (min-width: 1200px) {
  .photo-card {
    flex-basis: calc(25% - 15px);
  }
}

.card-link {
  flex-grow: 1;
  display: block;
  overflow: hidden;
}

.info {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 20px 7px 15px;
  display: flex;
}

.info-item {
  align-items: center;
  font-size: 13px;
  display: flex;
}

.info-icon {
  fill: rgba(77, 73, 73, .85);
  width: 18px;
  height: 18px;
  margin-right: 3px;
  padding: 0;
}

.load-more {
  cursor: pointer;
  color: #fff;
  transition: transform var(--animation-duration) var(--timing-function), border-radius var(--animation-duration) var(--timing-function);
  background-color: #4848d6;
  border: 1px solid #fff;
  border-radius: 5px;
  margin: 30px auto 50px;
  padding: 11px 35px;
  font-family: inherit;
  font-size: 15px;
  display: block;
}

.load-more:hover, .load-more:focus {
  border-radius: 10px;
  transform: scale(1.05);
}

.photo-card:hover {
  transform: scale(1.03);
}

.btn-scroll-up {
  width: 60px;
  height: 60px;
  color: #fff;
  cursor: pointer;
  transition: transform var(--animation-duration) var(--timing-function);
  background-color: #4848d6;
  border-radius: 50%;
  display: block;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

@media screen and (min-width: 768px) {
  .btn-scroll-up {
    bottom: 30px;
    right: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .btn-scroll-up {
    width: 70px;
    height: 70px;
    font-size: 17px;
    bottom: 40px;
    right: 40px;
  }
}

.btn-scroll-up:hover {
  transform: scale(1.05);
}

.hidden {
  display: none;
}

